<template>
  <transition name="slide">
    <div class="add-song" v-show="showFlag">
      <div class="title">
        <span class="text">添加歌曲到播放列表</span>
        <div class="close-icon" @click="hide">
          <i class="iconfont">&#xe60a;</i>
        </div>
      </div>
      
      <div class="search-box-wrapper">
        <search-box @query="onQueryChange" ref="searchBox" placeholder="搜索歌曲"></search-box>
      </div>

      <div class="shortcut">
        <switches :switches="switches" :currentIndex="currentIndex" @switch="setCurrentIndex"></switches>
        <div class="list-wrapper">
          <scroll class="list-scroll" v-if="currentIndex===0" ref="songList" :data="playHistory">
            <div class="list-inner">
              <song-list :songs="playHistory" @select="selectSong"></song-list>
            </div>
          </scroll>

          <scroll class="list-scroll" v-if="currentIndex===1" ref="searchList" :data="searchHistory">
            <div class="list-inner">
              <search-list @select="addQuery" @delete="deleteSearchHistory" :searches="searchHistory" :refreshDelay="refreshDelay"></search-list>
            </div>
          </scroll>
        </div>
      </div>

      <div class="search-result" v-show="query">
        <suggest :query="query" :showSinger="showSinger" @listScroll="blurInput" @select="selectSearch"></suggest>
      </div>
      
      <top-tip ref="topTip">
        <div class="tip-title">
          <span class="text">添加成功</span>
        </div>
      </top-tip>
    </div> 
  </transition>
</template>

<script type="text/ecmascript-6">
import SearchBox from '@/base/search-box/search-box'
import Suggest from '@/components/suggest/suggest'
import SongList from '@/base/song-list/song-list'
import SearchList from '@/base/search-list/search-list'
import Scroll from '@/base/scroll/scroll'
import Switches from '@/base/switches/switches'
import {searchMixin} from '@/common/js/mixin'
import {mapGetters,mapActions} from 'vuex'
import Song from '@/common/js/song'
import TopTip from "@/base/top-tip/top-tip"
export default {
  mixins:[searchMixin],
  data(){
    return {
      showFlag: false,
      currentIndex: 0,
      query: '',
      showSinger: false,
      switches:[
        {name:'最近播放'},
        {name:'搜索历史'}
      ]
    }
  },
  methods: {
    hide(){
      this.showFlag = false
    },
    show(){
      this.showFlag = true
      this.refreshList()
    },
    selectSong(song){
      this.insertSong(new Song(song))
      this.$refs.topTip.show()
    },
    selectSearch(song){
      this.saveSearch(song)
      this.$refs.topTip.show()
    },
    refreshList(){
      setTimeout(() => {
        if (this.currentIndex === 0) {
          this.$refs.songList.refresh()
        } else {
          this.$refs.searchList.refresh()
        }
      }, 20)
    },
    setCurrentIndex(index){
      this.currentIndex = index
    },
    ...mapActions([
      'insertSong'
    ])
  },
  computed:{
    ...mapGetters([
      'playHistory'
    ])
  },
  watch:{
    query(newQuery){
      if (!newQuery) {
        this.refreshList()
      }
    }
  },
  components: {
    SearchBox,
    Suggest,
    SongList,
    SearchList,
    Scroll,
    Switches,
    TopTip
  }
}
</script>

<style lang="stylus" ref="stylesheet/stylus" scoped>
  @import "~@/common/stylus/var"
  .add-song
    position fixed
    top 0
    bottom 0
    left 0
    right 0
    z-index 300
    background-color $color-background
    &.slide-enter-active, &.slide-leave-active
      transition all 0.3s
    &.slide-enter,&.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .title
      position relative
      height 44px
      text-align center
      background-color $color-theme
      .text
        line-height: 44px
        font-size: $font-size-large
        color: $color-white
      .close-icon
        position: absolute
        top: 0
        right: 8px
        .iconfont
          display block
          padding 12px
          font-size: 20px
          color: $color-theme-l
    .search-box-wrapper
      margin 20px
    .shortcut
      .list-wrapper
        position absolute
        top 165px
        bottom 0
        width 100%
        .list-scroll
          height: 100%
          overflow: hidden
          .list-inner
            padding: 20px 30px
    .search-result
      position: fixed
      top: 120px
      bottom: 0
      width: 100%
    .tip-title
      text-align center
      padding: 8px 0
      .text
        color $color-text-ll
        font-size $font-size-medium-x
</style>



