<template>
  <div class="my-header">
    <div class="icon"></div>
    <h1>Music</h1>
    <div class="user-icon" @click="userCenter">
      <div class="iconfont">&#xe60f;</div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    userCenter(){
      this.$router.push('/user')
    }
  }
}
</script>

<style scoped lang='stylus' rel='stylesheet/stylus'>
  @import "~@/common/stylus/var"
  .my-header
    position relative
    height 44px
    color $color-white
    text-align center
    background-color $color-theme
    .icon
      display inline-block
      vertical-align top
      margin-top 6px
      width 30px
      height 30px
      background url('./logo.png')
      background-size 30px 30px 
      margin-right 8px
    h1
      display inline-block
      vertical-align: top
      line-height 44px
      font-size: $font-size-large
    .user-icon
      position absolute
      top 6px
      right 6px
      .iconfont
        font-size 30px
        color $color-theme-l
</style>



